<template>
  <div>
    <h3 class="playlist-titel">最新评论</h3>
    <ul>
      <li v-for="(item, index) in comments" :key="index" class="comment-item">
        <div class="header">
          <img
            :src="`${item.user.avatarUrl}?imageView=1&type=webp&thumbnail=61x0`"
          />
        </div>
        <div class="fr-wrap">
          <div class="wrap-top">
            <div class="meta">
              <span class="nickname">{{ item.user.nickname }}</span>
              <div class="time">{{ item.time | dateFormat }}</div>
            </div>
            <div class="praise">
              <span>{{ item.likedCount == 0 ? "" : item.likedCount }}</span>
              <i class="iconfont icon-praise"></i>
            </div>
          </div>
          <div class="wrap-btm">
            <div class="hf" v-if="item.beReplied.length > 0">
              回复<span>@{{ item.beReplied[0].user.nickname }}</span>:
              {{ item.content }}
              <div class="text">
                @{{ item.beReplied[0].user.nickname }}
                ：
                {{ item.beReplied[0].content }}
              </div>
            </div>
            <div class="no-hf" v-else>{{ item.content }}</div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      comments: [],
      beReplied: [],
    };
  },
  methods: {
    getSongComment(id) {
      this.axios
        .get("https://apis.netstart.cn/music/comment/playlist", {
          params: {
            id,
            limit: 15,
          },
        })
        .then((res) => {
          //   console.log(res);
          this.comments = res.data.comments;
        });
    },
  },
  created() {
    this.getSongComment(this.$route.query.id);
  },
  filters: {
    dateFormat(time) {
      const times = new Date(time);
      const YY = times.getFullYear();
      const MM = times.getMonth() + 1;
      const DD = times.getDate();
      return `${YY}年${MM}月${DD}日`;
    },
  },
};
</script>

<style lang="less" scoped>
.playlist-titel {
  height: 23px;
  line-height: 23px;
  padding: 0 10px;
  font-size: 12px;
  color: #666;
  background: #eeeff0;
}
.comment-item {
  display: flex;
  padding-top: 10px;
  width: 100%;
  .header {
    height: 35px;
    margin: 0 10px;
    border-radius: 50%;
    img {
      width: 30px;
      height: 30px;
      border-radius: 50%;
    }
  }
  .fr-wrap {
    position: relative;
    padding: 0 10px 11px 0;
    flex: auto;
    width: 0;
    border-bottom: 1px solid #f3f3f3;
    .wrap-top {
      display: flex;
      justify-content: space-between;
      .nickname {
        font-size: 14px;
        color: #666;
      }
      .time {
        font-size: 12px;
        color: #999;
      }
      .praise {
        color: #999;
        span {
          font-size: 12px;
        }
        i {
          display: inline-block;
          margin-left: 6px;
          width: 16px;
          height: 16px;
          font-size: 14px;
          vertical-align: middle;
        }
      }
    }
    .wrap-btm {
      margin-top: 5px;
      font-size: 15px;
      line-height: 22px;
      text-align: justify;
      .hf {
        span {
          color: #507daf;
        }
        .text {
          margin: 5px 0;
          padding: 10px;
          font-size: 14px;
          color: #888;
          border: 1px solid #eee;
        }
      }
    }
  }
}
</style>