<template>
  <!-- 歌曲详情页 -->
  <div class="detail-page">
    <div
      class="mask"
      :style="{
        backgroundImage: `url(${
          currentSong.song
            ? currentSong.picUrl
            : currentSong.al
            ? currentSong.al.picUrl
            : currentSong.album.artist.img1v1Url
        }?imageView=1&type=webp&thumbnail=247x0)`,
      }"
    ></div>
    <span
      class="lower iconfont icon-lower"
      @click="$emit('toggle-page', false)"
    ></span>
    <div class="wrapper">
      <img
        class="needle"
        :class="{ paused: !playing }"
        src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
      />
      <div class="record" :class="{ playing: playing }">
        <img
          class="cover"
          :src="
            currentSong.song
              ? currentSong.picUrl
              : currentSong.al
              ? currentSong.al.picUrl
              : currentSong.album.artist.img1v1Url
          "
        />
        <img
          class="disc"
          src="https://s3.music.126.net/mobile-new/img/disc.png"
        />
      </div>
    </div>
    <!--  -->
    <!--  -->
    <div class="box">
      <div class="lyric-wrapper">
        <ul class="lyric-list">
          <li>{{ currentSong.name }}</li>
          <li v-for="(gc, index) in parsedLyric" :key="index">{{ gc.text }}</li>
        </ul>
      </div>
      <div class="btm">
        <span class="curTime" style="color: #fff">
          {{ currentTime | formatTime }}
        </span>
        <span class="durTime" style="color: #fff">
          {{ duration | formatTime }}
        </span>
        <input
          type="range"
          :max="duration"
          step="0.1"
          :value="value"
          @change="progress"
          @input="inputing = true"
        />

        <!-- 按钮控件 -->
        <div class="btn">
          <span
            class="iconfont icon-model"
            @click="$emit('toggle-pattern')"
          ></span>
          <span
            class="prev iconfont icon-prev"
            @click="$emit('prev-song')"
          ></span>
          <span
            class="iconfont"
            :class="[playing ? 'icon-play' : 'icon-pause']"
            @click="$emit('toggle-song')"
          ></span>

          <span
            class="next iconfont icon-next"
            @click="$emit('next-song')"
          ></span>
          <span
            class="iconfont icon-playList"
            @click.stop="$emit('toggle-popup', true)"
          ></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
  },
  data() {
    return {
      lyrics: "",
      value: this.currentTime,
      inputing: true,
    };
  },
  created() {
    this.getLyric(this.currentSong.id);
  },
  mounted() {
    this.progressBar = this.$refs.progressBar;
  },
  methods: {
    progress(event) {
      this.inputing = false;
      this.$emit("currentTime-change", event.target.value);
    },
    getLyric(id) {
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: { id },
        })
        .then((res) => {
          //   console.log(res);
          this.lyrics = res.data.lrc.lyric;
        });
    },
  },
  // 过滤时间格式
  filters: {
    formatTime(value) {
      const mins =
        Math.floor(value / 60) < 10
          ? `${Math.floor(value / 60)}`
          : Math.floor(value / 60);
      const sec =
        Math.floor(value % 60) < 10
          ? `${Math.floor(value % 60)}`
          : Math.floor(value % 60);
      return `${mins < 10 ? "0" + mins : mins}:${sec < 10 ? "0" + sec : sec}`;
    },
  },
  computed: {
    parsedLyric() {
      if (this.lyrics) {
        return this.lyrics
          .split("\n")
          .filter((s) => s)
          .map((s) => {
            var text = s.replace(/^\[\d{2}:\d{2}\.\d{2,3}\]/i, "");
            var timeStr = s.replace(text, "").replace(/(^\[|\]$)/g, "");
            var timeArr = timeStr.split(":").map((item) => Number(item));
            var time = timeArr[0] * 60 + timeArr[1];
            return { text, time };
          });
      } else {
        return [];
      }
    },
  },
  watch: {
    "currentSong.id"(id) {
      this.getLyric(id);
    },
    currentTime(newVal) {
      if (!this.inputing) {
        this.value = newVal;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.detail-page {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 40;
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    z-index: -2;
  }
  .lower {
    display: inline-block;
    margin-left: 10px;
    margin-top: 10px;
    font-size: 30px;
    color: #fff;
  }
  .mask {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    filter: blur(30px);
    transform: scale(1.5);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -1;
  }
  .wrapper {
    margin-top: 40px;
    .needle {
      position: absolute;
      top: 35px;
      left: 50%;
      margin-left: -22px;
      height: 115px;
      transform-origin: 10px 10px;
      transform: rotate(15deg);
      transition: all 1s;
      z-index: 10;
      &.paused {
        transform: rotate(-25deg);
      }
    }
    .record {
      position: relative;
      margin: 0 auto;
      width: 225px;
      height: 225px;
      animation: rotateImg 7s linear infinite;
      animation-play-state: paused;
      img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        &.cover {
          transform: scale(0.8);
        }
      }
      &.playing {
        animation-play-state: running;
      }
    }
  }
  .box {
    position: relative;
    height: 362px;
    overflow: hidden;
    .lyric-wrapper {
      width: 100%;
      height: 285px;
      color: #fff;
      text-align: center;
      background: rgba(0, 0, 0, 0.3);
      .lyric-list {
        height: 100%;
        overflow: hidden;
        li {
          padding: 0 10px;
          line-height: 32px;
          &:first-child {
            font-size: 18px;
            color: yellow;
            letter-spacing: 2px;
          }
        }
      }
    }
    .btm {
      position: relative;
      padding-top: 10px;
      height: 75px;
      background: rgba(0, 0, 0, 0.4);
      .curTime,
      .durTime {
        position: absolute;
        top: 6px;
        color: #ddd !important;
        font-size: 13px;
      }
      .curTime {
        left: 8px;
      }
      .durTime {
        right: 8px;
      }
      input {
        border-radius: 10px;
        display: block;
        margin: 0 auto;
        width: 285px;
        height: 5px;
      }
      .btn {
        position: absolute;
        bottom: 0px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 10px;
        width: 100%;
        span {
          font-size: 20px;
          color: #fff;
          text-shadow: 0px 0px 10px #999;
          &.prev,
          &.next {
            font-size: 25px;
          }
        }
      }
    }
  }
}
</style>