<template>
  <div class="ablum-title">
    <a href="javascript:history.go(-1);" class="iconfont icon-go-back"></a>
    <div
      class="bg-img"
      :style="{ backgroundImage: `url(${detail.coverImgUrl}?param=170y170)` }"
    ></div>
    <div class="wrapper">
      <div class="fl">
        <img :src="detail.coverImgUrl" />
        <span class="tips">歌单</span>
        <span class="num">{{ detail.playCount | playCounts }}</span>
      </div>
      <div class="fr">
        <h2>{{ detail.name }}</h2>
        <div class="source">
          <div class="img">
            <img :src="detail.creator.avatarUrl" />
          </div>
          <span>{{ detail.creator.nickname }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    detail: {
      type: Object,
    },
  },
  data() {
    return {};
  },
  filters: {
    playCounts(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.posab() {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.ablum-title {
  position: relative;
  padding: 30px 10px 30px 15px;
  overflow: hidden;
  a {
    position: absolute;
    top: 4px;
    font-size: 22px;
    color: #eee;
    text-decoration: none;
    z-index: 2;
  }
  .bg-img {
    .posab();
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    filter: blur(20px);
    transform: scale(1.5);
    &::after {
      content: " ";
      .posab();
      background-color: rgba(0, 0, 0, 0.25);
    }
  }
  .wrapper {
    display: flex;
    position: relative;
    height: 126px;
    color: #fff;
    z-index: 5;
    .fl {
      position: relative;
      width: 126px;
      height: 126px;
      img {
        width: 100%;
      }
      .tips {
        position: absolute;
        top: 10px;
        left: 0;
        padding: 0 8px;
        height: 17px;
        font-size: 12px;
        text-align: center;
        line-height: 17px;
        border-top-right-radius: 17px;
        border-bottom-right-radius: 17px;
        background: rgba(217, 48, 48, 0.8);
        z-index: 6;
      }
      .num {
        position: absolute;
        right: 3px;
        top: 2px;
        padding-left: 15px;
        font-size: 12px;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 15px;
          height: 15px;
          background: url("data:image/svg+xml;base64,");
          background-repeat: no-repeat;
          background-size: 11px 10px;
          background-position: 2px;
        }
      }
    }
    .fr {
      flex: 1;
      margin-left: 16px;
      padding-top: 3px;
      h2 {
        font-size: 17px;
        line-height: 1.3;
        height: 44px;
      }
      .source {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        color: rgba(255, 255, 255, 0.7);
        .img {
          vertical-align: middle;
          display: inline-block;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          margin-right: 3px;
          overflow: hidden;
        }
        span {
          display: -webkit-inline-box;
          width: 120px;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
          text-overflow: ellipsis;
          vertical-align: middle;
        }
      }
    }
  }
}
</style>