<template>
  <div class="footer">
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <PlayPage
        v-if="showPage"
        @toggle-page="showPage = $event"
        @toggle-popup="showMask = $event"
        :currentSong="currentSong"
        :currentTime="currentTime"
        :duration="duration"
        :playing="playing"
        @toggle-song="$emit('toggle-song')"
        @next-song="$emit('next-song')"
        @prev-song="$emit('prev-song')"
        @toggle-pattern="$emit('toggle-pattern')"
        @currentTime-change="$emit('currentTime-change', $event)"
      />
    </transition>

    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <PlayBar
        v-show="!showPage"
        :currentList="currentList"
        :currentSong="currentSong"
        :currentTime="currentTime"
        :duration="duration"
        :playing="playing"
        @toggle-song="$emit('toggle-song')"
        @toggle-popup="showMask = $event"
        @toggle-page="showPage = $event"
      />
    </transition>

    <PlayPopup
      v-if="showMask"
      :currentList="currentList"
      :currentSong="currentSong"
      :playing="playing"
      @toggle-popup="showMask = $event"
      @play-song="$emit('play-song', $event)"
    />
  </div>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import PlayPage from "@/components/PlayPage.vue";
import PlayPopup from "@/components/PlayPopup.vue";
export default {
  components: {
    PlayBar,
    PlayPage,
    PlayPopup,
  },
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    currentList: Array,
  },
  data() {
    return {
      showMask: false,
      showPage: false,
    };
  },
};
</script>

<style lang="less" scoped>
</style>