<template>
  <li v-if="item.song" @click="$emit('play-song', item)">
    <div class="sgfl">
      <div class="sg-title">
        {{ item.name }}
        <span v-for="n in item.song.alias" :key="n">{{ n }}</span>
        <!-- 别名 -->
      </div>
      <div class="sg-info">
        <em v-if="item.song.exclusive"></em>
        <!-- 独家 -->
        <span v-for="artist in item.song.artists" :key="artist.id">
          {{ artist.name }}
          <!-- 作者 -->
        </span>
        <i>{{ item.song.album.name }}</i>
        <!-- 专辑 -->
      </div>
    </div>
    <div class="sgfr">
      <div class="icon">
        <div
          class="play"
          :class="{ current: currentSongId === item.id, playing: playing }"
        >
          <i></i>
          <i></i>
          <i></i>
        </div>
      </div>
    </div>
  </li>

  <li v-else-if="item.alia" @click="$emit('play-song', item)">
    <div class="num"><slot></slot></div>
    <div class="sgfl">
      <div class="sg-title">
        {{ item.name }}
        <span v-for="n in item.alia" :key="n">{{ n }}</span>
      </div>
      <div class="sg-info">
        <span v-for="artist in item.ar" :key="artist.id">
          {{ artist.name }}
        </span>
        <i>{{ item.al.name }}</i>
      </div>
    </div>
    <div class="sgfr">
      <div class="icon">
        <div
          class="play"
          :class="{ current: currentSongId === item.id, playing: playing }"
        >
          <i></i>
          <i></i>
          <i></i>
        </div>
      </div>
    </div>
  </li>

  <li v-else @click="$emit('play-song', item)">
    <div class="sgfl">
      <div class="sg-title">
        {{ item.name }}
      </div>
      <div class="sg-info">
        <span v-for="artist in item.artists" :key="artist.id">
          {{ artist.name }}
        </span>
        <i>{{ item.album.name }}</i>
      </div>
    </div>
    <div class="sgfr">
      <div class="icon">
        <div
          class="play"
          :class="{ current: currentSongId === item.id, playing: playing }"
        >
          <i></i>
          <i></i>
          <i></i>
        </div>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    currentSongId: {
      type: Number,
    },
    playing: {
      playing: Boolean,
    },
  },
};
</script>

<style lang="less" scoped>
li {
  display: flex;
  align-items: center;
  margin-left: 10px;
  border-bottom: 1px solid #ececed;
  .num {
    padding: 5px 12px 5px 0;
  }
  &.lt {
    .num {
      color: #d43c33;
    }
  }
  .sgfl {
    padding: 6px 0;
    flex: 1 1 auto;
    .sg-title {
      font-size: 17px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      span {
        color: #888;
        &:before {
          content: "(";
        }
        &:after {
          content: ")";
        }
      }
    }
    .sg-info {
      font-size: 12px;
      color: #888;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      em {
        display: inline-block;
        width: 12px;
        height: 8px;
        background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png")
          no-repeat;
        background-size: 166px auto;
        margin-right: 4px;
      }
      span {
        &:after {
          content: "/";
        }
        &:last-of-type {
          &:after {
            content: "-";
            margin-right: 3px;
          }
        }
      }
      i {
        font-style: normal;
      }
    }
  }
  .sgfr {
    display: flex;
    padding: 0 10px;
    .icon {
      width: 22px;
      height: 22px;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      .play {
        width: 100%;
        height: 100%;
        background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png")
          no-repeat;
        background-size: 166px auto;
        background-position: -24px 0;
      }
      .current {
        width: 15px;
        height: 15px;
        display: flex;
        justify-content: space-between;
        background: none;
        i {
          width: 3px;
          height: 100%;
          background: #d43c33;
          transform-origin: bottom;
          animation: playing 0.9s linear 0s infinite alternate;
          animation-play-state: paused;
          &:nth-child(1) {
            animation-delay: -0.6s;
          }
          &:nth-child(2) {
            animation-delay: -0.3s;
          }
        }
        &.playing {
          i {
            animation-play-state: running;
          }
        }
      }
    }
  }
}
@keyframes playing {
  from {
    transform: scaleY(0.2);
  }
  to {
    transform: scaleY(1);
  }
}
</style>