          :playing="playing"
          @play-song="$emit('play-song', $event)"
        ></SongList>
      </ul>
    </div>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";

export default {
  components: {
    SongList,
  },
  props: {
    showMask: Boolean,
    currentList: Array,
    currentSong: Object,
    playing: Boolean,
  },
};
</script>

