<template>
  <li @click="$router.push('/playlist?id=' + item.id)" class="card-item">
    <div class="content">
      <img
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=247x0`"
        class="imgs"
      />
      <span>
        {{ item.playCount | playCounts }}
      </span>
      <p>{{ item.name }}</p>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  filters: {
    playCounts(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.fl {
  float: left;
}

.card-item {
  width: 33.3%; 
  padding: 0 1px 0 1px;
  margin-bottom: 16px;
  .content {
    position: relative;
    top: 0;
    left: 0;
    span {
      position: absolute;
      right: 5px;
      top: 5px;
      padding-left: 15px;
      font-size: 12px;
      color: #fff;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 15px;
        height: 15px;
        background: url("data:image/svg+xml;base64,");
        background-repeat: no-repeat;
        background-size: 11px 10px;
        background-position: 2px;
      }
    }

    p {
      padding: 6px 2px 0 6px;
      font-size: 13px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
</style>