<template>
  <div class="footer">
    <!-- 播放信息 -->
    <div
      class="footer-bar animate__animated animate__fadeInUp"
      :class="{ playing: playing }"
      @click="$emit('toggle-page', true)"
    >
      <img
        :src="`${
          currentSong.song
            ? currentSong.picUrl
            : currentSong.al
            ? currentSong.al.picUrl
            : currentSong.album.artist.img1v1Url
        }?imageView=1&type=webp&thumbnail=247x0`"
      />
      <h3>
        <span class="first">{{ currentSong.name }}</span>
        <span>{{
          currentSong.song
            ? currentSong.song.artists[0].name
            : currentSong.ar
            ? currentSong.ar[0].name
            : currentSong.artists[0].name
        }}</span>
      </h3>
      <div class="btn-box">
        <div class="progress" @click.stop="$emit('toggle-song')">
          <canvas width="40" height="40" ref="progressBar"></canvas>
          <div
            class="iconfont"
            :class="[playing ? 'icon-play' : 'icon-pause']"
          ></div>
        </div>
        <div
          class="iconfont icon-playList"
          @click.stop="$emit('toggle-popup', true)"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },

  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    currentList: Array,
  },
  mounted() {
    this.progressBar = this.$refs.progressBar;
  },
  computed: {
    proportion() {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    proportion(pro) {
      var context = this.$refs.progressBar.getContext("2d");
      context.clearRect(0, 0, 40, 40);
      context.beginPath();
      context.arc(
        20,
        20,
        18,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * pro - 90)
      );
      context.strokeStyle = "orange";
      context.stroke();

      context.beginPath();
      context.arc(
        20,
        20,
        18,
        (Math.PI / 180) * (360 * pro - 90),
        (Math.PI / 180) * (360 - 90)
      );
      context.strokeStyle = "#000";
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
.animate__animated {
  animation-direction: 0.5s;
}

.footer {
  .footer-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    bottom: 0;
    padding: 0 10px;
    width: 100%;
    height: 50px;
    background: #fcfcfd;
    box-shadow: 0px -2px 20px #ddd;
    z-index: 30;
    img {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      animation: rotateImg 7s linear infinite;
      animation-play-state: paused;
    }
    h3 {
      width: 170px;
      font-size: 14px;
      text-align: center;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      span {
        font-size: 12px;
        color: #999;
      }
      .first {
        font-size: 14px;
            color: #666;

        &::after {
          content: " - ";
        }
      }
    }
    .btn-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #999;
      .progress {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        margin-right: 35px;
        width: 40px;
        height: 40px;
        canvas {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
        .iconfont {
          &.icon-pause {
            margin-left: 4px;
          }
        }
      }
      .icon-playList {
        margin-top: 5px;
        font-size: 18px;
      }
    }
    &.playing {
      img {
        animation-play-state: running;
      }
    }
  }
}
</style>