<template>
  <div class="home-outer">
    <div class="home">
      <HomeTitle>编辑推荐</HomeTitle>
      <ul class="home-card">
        <HomeCard
          v-for="item in personalizeds"
          :key="item.id"
          :item="item"
        ></HomeCard>
      </ul>
      <HomeTitle>最新音乐</HomeTitle>
      <ul class="music-list">
        <SongList
          v-for="item in newsongs"
          :key="item.id"
          :item="item"
          @play-song="
            $emit('play-song', $event);
            $emit('mask-box', newsongs);
          "
          :currentSongId="currentSongId"
          :playing="playing"
        ></SongList>
      </ul>
      <div class="openapp">
        <div class="download">打开APP，发现更多好音乐 ></div>
      </div>
    </div>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import HomeCard from "@/components/HomeCard.vue";
import SongList from "@/components/SongList.vue";
export default {
  name: "Home",
  components: {
    HomeTitle,
    HomeCard,
    SongList,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  data() {
    return {
      personalizeds: [],
      newsongs: [],
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/personalized?limit=6")
      .then((res) => {
        //   console.log(res);
        this.personalizeds = res.data.result;
      });
    this.axios
      .get("https://apis.netstart.cn/music/personalized/newsong")
      .then((res) => {
        // console.log(res);
        this.newsongs = res.data.result;
      });
  },
};
</script>

<style lang="less" scoped>
.home-outer {
  .home {
    .home-card {
      display: flex;
      flex-wrap: wrap;
    }
    .music-list {
      padding-bottom: 10px;
    }
    .openapp {
      display: flex;
      flex-flow: column;
      justify-content: center;
      width: 100%;
      height: 26px;
      .download {
        margin: 0 auto;
        width: 230px;
        height: 30px;
        font-size: 13px;
        text-align: center;
        line-height: 30px;
        border: 1px solid #d33a31;
        border-radius: 38px;
        color: #d33a31;
      }
    }
  }
}
