<template>
  <div v-if="detail">
    <AlbumTitle :detail="detail"></AlbumTitle>
    <h3 class="playlist-titel">歌曲列表</h3>
    <ul>
      <SongList
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        @play-song="
          $emit('play-song', item);
          $emit('mask-box', detail.tracks);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        :class="{ lt: index < 3 }"
        >{{ index + 1 }}
      </SongList>
    </ul>
    <SongHotComments></SongHotComments>
    <SongComment></SongComment>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";
import AlbumTitle from "@/components/AlbumTitle.vue";
import SongComment from "@/components/SongComment.vue";
import SongHotComments from "@/components/SongHotComments.vue";

export default {
  components: {
    SongList,
    AlbumTitle,
    SongComment,
    SongHotComments,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  data() {
    return {
      detail: null,
    };
  },
  methods: {
    getPlayListDetail(id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
        //   console.log(res);
          this.detail = res.data.playlist;
        });
    },
  },
  created() {
    this.getPlayListDetail(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.playlist-titel {
  height: 23px;
  line-height: 23px;
  padding: 0 10px;
  font-size: 12px;
  color: #666;
  background: #eeeff0;
}

ul {
  padding-bottom: 10px;
}
</style>