<template>
  <div id="app">
    <div class="nav-bar" v-if="$route.meta.navBar">
      <ul id="nav">
        <li class="first">
          <i class="iconfont icon-wy-logo"></i>
          <span class="download">下载APP</span>
        </li>
        <li>
          <router-link to="/">推荐音乐</router-link>
        </li>
        <li>
          <router-link to="/hot">热歌榜</router-link>
        </li>
        <li>
          <router-link to="/search">搜索</router-link>
        </li>
      </ul>
    </div>

    <router-view
      @play-song="playSong"
      @mask-box="maskBox"
      :currentSongId="currentSong ? currentSong.id : null"
      :playing="playing"
    />
    <audio
      ref="audio"
      :src="currentSongSrc"
      controls
      autoplay
      @playing="playing = true"
      @pause="playing = false"
      @timeupdate="timeupdate"
      @durationchange="durationchange"
    ></audio>
    <Play
      v-if="currentSong"
      :playing="playing"
      :currentSong="currentSong"
      :currentList="currentList"
      @play-song="playSong"
      @toggle-song="toggleSong"
      :currentTime="currentTime"
      :duration="duration"
      @next-song="nextSong"
      @prev-song="prevSong"
      @currentTime-change="$refs.audio.currentTime = $event"
    ></Play>
  </div>
</template>

<script>
import Play from "@/components/Play.vue";

export default {
  components: {
    Play,
  },
  data() {
    return {
      currentSong: null,
      currentList: [],
      playing: false,
      currentTime: 0,
      duration: 0,
    };
  },
  computed: {
    currentSongSrc() {
      if (this.currentSong) {
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`;
      } else {
        return null;
      }
    },
  },
  methods: {
    playSong(song) {
      this.currentSong = song;
    },
    maskBox(list) {
      this.currentList = list;
    },
    toggleSong() {
      if (this.playing) {
        this.$refs.audio.pause();
      } else {
        this.$refs.audio.play();
      }
    },
    timeupdate() {
      this.currentTime = this.$refs.audio.currentTime;
    },
    durationchange() {
      this.duration = this.$refs.audio.duration;
    },
    nextSong() {
      var index = this.currentList.findIndex((item) => {
        return item.id === this.currentSong.id;
      });
      index++;
      index = index >= this.currentList.length ? 0 : index;
      this.playSong(this.currentList[index]);
      //   console.log('下',index);
    },
    prevSong() {
      var index = this.currentList.findIndex((item) => {
        return item.id === this.currentSong.id;
      });
      index--;
      index = index < 0 ? this.currentList.length - 1 : index;
      this.playSong(this.currentList[index]);
      //   console.log('上',index);
    },
  },
};
</script>

