<template>
  <div class="search">
    <div class="search-box">
      <div class="input-cont">
        <i class="iconfont icon-sousuo"></i>
        <input
          type="search"
          placeholder="搜索歌手、歌曲、专辑"
          v-model.trim="value"
          @keyup.enter="value && (searchs = true)"
          @focus="ipt = true"
          @blur="ipt = false"
        />
      </div>
    </div>

    <section class="hotlist" v-if="!value && !searchs">
      <h3 class="narrow">热门搜索</h3>
      <ul class="list">
        <li
          v-for="item in hots"
          :key="item.first"
          @click="
            searchs = true;
            value = item.first;
          "
        >
          {{ item.first }}
        </li>
      </ul>
      <ul class="history">
        <li
          v-for="(ls, index) in history"
          :key="ls"
          @click="
            searchs = true;
            value = ls;
          "
        >
          <i class="iconfont icon-history"></i>
          <span>{{ ls }}</span>
          <b class="iconfont icon-delete" @click.stop="del(index)"></b>
        </li>
      </ul>
    </section>

    <section class="proposal" v-if="value && !searchs">
      <h3>搜索建议</h3>
      <ul class="list">
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="
            searchs = true;
            value = item.keyword;
          "
        >
          <i class="iconfont icon-sousuo"></i>
          <span>{{ item.keyword }}</span>
        </li>
      </ul>
    </section>

    <section class="suggests" v-if="searchs">
      <h3>搜索结果</h3>
      <ul class="list">
        <SongList
          v-for="item in searchResult"
          :key="item.id"
          :item="item"
          @play-song="
            $emit('play-song', item);
            $emit('mask-box', searchResult);
          "
          :currentSongId="currentSongId"
          :playing="playing"
        ></SongList>
      </ul>
      <p v-if="!hasMore">没有更多了</p>
    </section>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";

export default {
  name: "Search",
  components: { SongList },
  props: {
    currentSongId: Number,
    playing: Boolean,
  },
  data() {
    return {
      value: "",
      hots: [],
      suggests: [],
      searchs: false,
      searchResult: [],
      ipt: false,
      page: 0,
      hasMore: false,
      history: JSON.parse(window.localStorage.getItem("history")) || [],
    };
  },
  created() {
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.hots = res.data.result.hots;
    });
  },
  methods: {
    getSearch() {
      this.axios
        .get("https://apis.netstart.cn/music/search", {
          params: {
            keywords: this.value,
            limit: 25,
            offset: this.page * 30,
          },
        })
        .then((res) => {
        //   console.log(res);
          this.searchResult.push(...res.data.result.songs);
          this.page++;
          this.hasMore = res.data.result.hasMore;
        });

      this.history = [...new Set([...this.history, this.value])];
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
    del(index) {
      this.history.splice(index, 1);
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
  },
  watch: {
    value(newVal) {
      if (this.ipt) {
        this.searchs = false;
      }
      if (newVal && !this.searchs) {
        this.axios
          .get("https://apis.netstart.cn/music/search/suggest", {
            params: {
              keywords: newVal,
              type: "mobile",
            },
          })
          .then((res) => {
            this.suggests = res.data.result.allMatch;
          });
      } else {
        this.suggests = [];
      }
    },
    searchs(newVal) {
      if (newVal && this.value) {
        this.getSearch();
      } else {
        this.searchResult = [];
      }
    },
  },
};
</script>

<style lang="less" scoped>
h3 {
  font-size: 14px;
  line-height: 40px;
  color: #0b3a92;
  border-bottom: 1px solid #eee;
}

.search-box {
  position: relative;
  width: 100%;
  padding: 15px 10px;
  .input-cont {
    position: relative;
    width: 100%;
    height: 30px;
    padding: 0 30px;
    box-sizing: border-box;
    background: #ebecec;
    border-radius: 30px;
    i {
      position: absolute;
      left: 5px;
      top: 4px;
      font-size: 22px;
      color: #c9c9c9;
      transform: rotate(-5deg);
    }
    input {
      width: 100%;
      height: 30px;
      font-size: 14px;
      color: #333;
      line-height: 18px;
      border: none;
      background: none;
      outline: none;
      &::placeholder {
        font-size: 14px;
        color: #c9c9c9;
      }
    }
  }
}

.hotlist {
  padding: 15px 10px 0;
  .narrow {
    font-size: 12px;
    line-height: 12px;
    color: #666;
    border: none;
  }
  .list {
    margin: 10px 0 7px;
    li {
      list-style: none;
      display: inline-block;
      height: 32px;
      margin: 0 8px 8px 0;
      padding: 0 14px;
      font-size: 14px;
      line-height: 32px;
      color: #333;
      border: 1px solid #d3d4da;
      border-radius: 32px;
    }
  }
  .history {
    list-style: none;
    padding-right: 10px;
    li {
      position: relative;
      display: flex;
      align-items: center;
      margin-left: 24px;
      height: 45px;
      line-height: 45px;
      color: #666;
      border-bottom: 1px solid #eee;
      i {
        position: absolute;
        left: -26px;
        font-size: 24px;
        color: #ccc;
      }
      span {
        margin-left: 4px;
        font-size: 14px;
      }
      b {
        position: absolute;
        right: 10px;
        font-size: 24px;
        color: #999;
        font-weight: normal;
      }
    }
  }
}
.proposal {
  padding-left: 10px;
  color: #333;
  li {
    position: relative;
    display: flex;
    align-items: center;
    margin-left: 20px;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #ddd;
    i {
      position: absolute;
      left: -27px;
      font-size: 24px;
      color: #ccc;
    }
    span {
      margin-right: 4px;
      font-size: 14px;
    }
  }
}
.suggests {
  padding-left: 10px;
  li {
    padding: 5px;
    line-height: 24px;
  }
}
</style>